___CSS_LOADER_EXPORT___.push([module.id, `/* Estilos generales  */
body {
    font-family: arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Estilos para el contenedor principal  */
#app {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/*  Estilos para el formulario */
form {
    display: flex;
    justify-content: space-between;

}

/* Estilos al input*/
input {
    width: 75%;
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ddd;
    border-radius: 3px;

}

/* estilos al bot贸n*/
button{
    padding: 5px 8px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

/* estilos a la lista*/
ul {
list-style: none;
padding: 0;

}
/* estilos a los elementos de la lista*/
li{
    background-color: #f4f4f4;
    padding: 10px;
    border: 1px solid #ddd;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

/* estilos para cuanso se complete la tarea */
li.completed {
    text-decoration: line-through;
    color: #888;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,uBAAuB;AACvB;IACI,8BAA8B;IAC9B,yBAAyB;IACzB,SAAS;IACT,UAAU;IACV,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA,0CAA0C;AAC1C;IACI,uBAAuB;IACvB,aAAa;IACb,kBAAkB;IAClB,uCAAuC;AAC3C;;AAEA,gCAAgC;AAChC;IACI,aAAa;IACb,8BAA8B;;AAElC;;AAEA,oBAAoB;AACpB;IACI,UAAU;IACV,YAAY;IACZ,kBAAkB;IAClB,sBAAsB;IACtB,kBAAkB;;AAEtB;;AAEA,oBAAoB;AACpB;IACI,gBAAgB;IAChB,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,eAAe;AACnB;;AAEA,sBAAsB;AACtB;AACA,gBAAgB;AAChB,UAAU;;AAEV;AACA,uCAAuC;AACvC;IACI,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;;AAEvB;;AAEA,6CAA6C;AAC7C;IACI,6BAA6B;IAC7B,WAAW;AACf","sourcesContent":["/* Estilos generales  */\r\nbody {\r\n    font-family: arial, sans-serif;\r\n    background-color: #f4f4f4;\r\n    margin: 0;\r\n    padding: 0;\r\n    height: 100vh;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n/* Estilos para el contenedor principal  */\r\n#app {\r\n    background-color: white;\r\n    padding: 20px;\r\n    border-radius: 5px;\r\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n/*  Estilos para el formulario */\r\nform {\r\n    display: flex;\r\n    justify-content: space-between;\r\n\r\n}\r\n\r\n/* Estilos al input*/\r\ninput {\r\n    width: 75%;\r\n    padding: 8px;\r\n    margin-right: 10px;\r\n    border: 1px solid #ddd;\r\n    border-radius: 3px;\r\n\r\n}\r\n\r\n/* estilos al bot贸n*/\r\nbutton{\r\n    padding: 5px 8px;\r\n    background-color: #007bff;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 3px;\r\n    cursor: pointer;\r\n}\r\n\r\n/* estilos a la lista*/\r\nul {\r\nlist-style: none;\r\npadding: 0;\r\n\r\n}\r\n/* estilos a los elementos de la lista*/\r\nli{\r\n    background-color: #f4f4f4;\r\n    padding: 10px;\r\n    border: 1px solid #ddd;\r\n    margin-top: 10px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r